const baseUrl = "https://r4xp4nhmja.execute-api.eu-west-2.amazonaws.com/default";

const DoLogs = true

function Log(...args) {
    if (DoLogs) {
        console.log(...args)
    }
}

const Fetch = function(url, init) {
    let headers

    if (init) {
        if (init.headers) {
            headers = init.headers
        } else {
            headers = {}
        }
    } else {
        headers = {}
    }

    Log(init)
    Log(headers)

    return fetch(`${baseUrl}${url}`, { ...init, headers })
}

const APIRequest = async function(method, body) {
    Log(body)
    Log(method)

    const http_response = await Fetch("", {
        method: "POST",
        headers: {
            "Content-Type": "Application/json"
        },
        body: JSON.stringify({ method, body })
    })

    if (!http_response.ok) {
        throw Error("APIRequest: Couldn't get '" + method + "' response")
    }

    const json_response = await http_response.json()

    Log("Response:", json_response)

    const { result, status } = json_response

    if (status !== "ok") {
        throw Error("APIRequest: '" + method + "' request failed")
    }

    return result
}

const APIClient = (function() {
    return {
        NewCharacter: function(id, desc) {
            let method = "new_character"
            let params = {id: id, method: method}
            return APIRequest(method, params)
        },

        ChangeMood: function(id, new_mood) {
            let method = "change_mood"
            let params = {id: id, new_mood: new_mood}
            return APIRequest(method, params)
        },

        TriggerEvent: function(id, event_desc, attitude) {
            let method = "trigger_event"
            let params = {id: id, event_desc: event_desc, attitude: attitude}
            return APIRequest(method, params)
        },

        GetReplyFromCharacter: function(id, user_message, user_name) {
            let method = "request_reply"
            let params = {id: id, user_message: user_message, user_name: user_name}
            return APIRequest(method, params)
        },

        CreateUserSession: function() {
            let method = "create_user_session"
            let params = {}
            return APIRequest(method, params)
        },

        
    }
})()

export default APIClient